import React from 'react';
import { TweenMax } from 'gsap';

import imagesLoaded from 'imagesloaded';

import '../css/item.scss';

class ViewCmp extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
            isLoaded: false
        }
    }

    componentDidMount(){

        let showSpeed = 500;
        TweenMax.from(this.view, showSpeed/1000, 
            {
                opacity: 0,
                scale: 1.1,
                display: 'block'
            }
        );    
        
        // view page scroll top
        window.scrollTo({'top':0});


        // imagesLoaded안에서 this가 바뀌므로 this를 _this에 저장해서 사용
        let _this = this;

        // imagesLoaded / Detect when images have been loaded.
        imagesLoaded('img', function(){
            _this.setState({
                isLoaded: !_this.state.isLoaded
            });
        });
            
    }

    componentWillUnmount(){
        // console.log('will unmount');
    }


    render(){

        return (

            <React.Fragment>
                {/* loading */}
                {this.state.isLoaded ? 
                    ''
                :
                    <div className="loading_box">
                        <span>Loading ...</span>
                    </div>
                }
                
                <div className="item_view" ref={view => this.view = view}>
                    <div className="inner">
                        <div className="visual">
                            <img src={this.props.img}/>
                        </div>
                        <div className="desc">
                            <p><strong>{this.props.type}</strong></p>
                            <p>{this.props.cate}</p>
                            <p>{this.props.title}</p>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default ViewCmp;